"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import { useRouter } from "next/navigation"

interface BookingForm {
  pickup: string
  dropoff: string
  vehicleType: string
  scheduledTime: string
  notes: string
}

interface FareEstimate {
  distance: string
  duration: string
  baseFare: number
  totalFare: number
}

export default function BookRidePage() {
  const router = useRouter()
  const [formData, setFormData] = useState<BookingForm>({
    pickup: '',
    dropoff: '',
    vehicleType: '',
    scheduledTime: '',
    notes: ''
  })
  const [fareEstimate, setFareEstimate] = useState<FareEstimate | null>(null)
  const [loading, setLoading] = useState(false)
  const [estimating, setEstimating] = useState(false)
  const [errors, setErrors] = useState<Partial<BookingForm>>({})

  const vehicleTypes = [
    { id: 'economy', name: 'Economy', description: 'Affordable rides', multiplier: 1.0 },
    { id: 'comfort', name: 'Comfort', description: 'More space and comfort', multiplier: 1.3 },
    { id: 'premium', name: 'Premium', description: 'Luxury vehicles', multiplier: 1.8 }
  ]

  const handleInputChange = (field: keyof BookingForm, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
    
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }))
    }

    // Auto-estimate fare when pickup and dropoff are filled
    if ((field === 'pickup' || field === 'dropoff') && formData.pickup && formData.dropoff) {
      estimateFare()
    }
  }

  const estimateFare = async () => {
    if (!formData.pickup || !formData.dropoff) return

    setEstimating(true)
    
    // Simulate API call for fare estimation
    setTimeout(() => {
      const baseDistance = Math.random() * 20 + 2 // 2-22 km
      const baseDuration = Math.random() * 30 + 10 // 10-40 minutes
      const baseFare = 5.0
      const perKmRate = 1.5
      const vehicleMultiplier = vehicleTypes.find(v => v.id === formData.vehicleType)?.multiplier || 1.0
      
      const totalFare = (baseFare + (baseDistance * perKmRate)) * vehicleMultiplier

      setFareEstimate({
        distance: `${baseDistance.toFixed(1)} km`,
        duration: `${Math.round(baseDuration)} min`,
        baseFare: baseFare + (baseDistance * perKmRate),
        totalFare: totalFare
      })
      setEstimating(false)
    }, 1500)
  }

  const validateForm = (): boolean => {
    const newErrors: Partial<BookingForm> = {}

    if (!formData.pickup.trim()) {
      newErrors.pickup = 'Pickup location is required'
    }
    if (!formData.dropoff.trim()) {
      newErrors.dropoff = 'Drop-off location is required'
    }
    if (!formData.vehicleType) {
      newErrors.vehicleType = 'Please select a vehicle type'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return

    setLoading(true)

    try {
      // Simulate API call to book ride
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      // Mock successful booking
      const rideId = 'ride-' + Math.random().toString(36).substr(2, 9)
      
      // Redirect to tracking page
      router.push(`/customer/track/${rideId}`)
    } catch (error) {
      console.error('Booking failed:', error)
      // Handle error - show error message
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-black mb-2">Book a Ride</h1>
          <p className="text-gray-600">Enter your trip details to get started</p>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Booking Form */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <CardTitle>Trip Details</CardTitle>
                <CardDescription>Fill in your pickup and destination</CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleSubmit} className="space-y-6">
                  {/* Pickup Location */}
                  <div className="space-y-2">
                    <Label htmlFor="pickup">Pickup Location</Label>
                    <Input
                      id="pickup"
                      placeholder="Enter pickup address"
                      value={formData.pickup}
                      onChange={(e) => handleInputChange('pickup', e.target.value)}
                      className={errors.pickup ? 'border-red-500' : ''}
                    />
                    {errors.pickup && (
                      <p className="text-sm text-red-600">{errors.pickup}</p>
                    )}
                  </div>

                  {/* Drop-off Location */}
                  <div className="space-y-2">
                    <Label htmlFor="dropoff">Drop-off Location</Label>
                    <Input
                      id="dropoff"
                      placeholder="Enter destination address"
                      value={formData.dropoff}
                      onChange={(e) => handleInputChange('dropoff', e.target.value)}
                      className={errors.dropoff ? 'border-red-500' : ''}
                    />
                    {errors.dropoff && (
                      <p className="text-sm text-red-600">{errors.dropoff}</p>
                    )}
                  </div>

                  {/* Vehicle Type */}
                  <div className="space-y-2">
                    <Label htmlFor="vehicleType">Vehicle Type</Label>
                    <Select 
                      value={formData.vehicleType} 
                      onValueChange={(value) => handleInputChange('vehicleType', value)}
                    >
                      <SelectTrigger className={errors.vehicleType ? 'border-red-500' : ''}>
                        <SelectValue placeholder="Select vehicle type" />
                      </SelectTrigger>
                      <SelectContent>
                        {vehicleTypes.map((vehicle) => (
                          <SelectItem key={vehicle.id} value={vehicle.id}>
                            <div>
                              <div className="font-medium">{vehicle.name}</div>
                              <div className="text-sm text-gray-500">{vehicle.description}</div>
                            </div>
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                    {errors.vehicleType && (
                      <p className="text-sm text-red-600">{errors.vehicleType}</p>
                    )}
                  </div>

                  {/* Scheduled Time (Optional) */}
                  <div className="space-y-2">
                    <Label htmlFor="scheduledTime">Scheduled Time (Optional)</Label>
                    <Input
                      id="scheduledTime"
                      type="datetime-local"
                      value={formData.scheduledTime}
                      onChange={(e) => handleInputChange('scheduledTime', e.target.value)}
                      min={new Date().toISOString().slice(0, 16)}
                    />
                    <p className="text-sm text-gray-500">Leave empty for immediate pickup</p>
                  </div>

                  {/* Notes */}
                  <div className="space-y-2">
                    <Label htmlFor="notes">Special Instructions (Optional)</Label>
                    <Textarea
                      id="notes"
                      placeholder="Any special instructions for the driver..."
                      value={formData.notes}
                      onChange={(e) => handleInputChange('notes', e.target.value)}
                      rows={3}
                    />
                  </div>

                  {/* Submit Button */}
                  <Button 
                    type="submit" 
                    className="w-full bg-black hover:bg-gray-800 text-white h-12"
                    disabled={loading}
                  >
                    {loading ? (
                      <div className="flex items-center">
                        <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
                        Booking Ride...
                      </div>
                    ) : (
                      'Book Ride Now'
                    )}
                  </Button>
                </form>
              </CardContent>
            </Card>
          </div>

          {/* Fare Estimate */}
          <div>
            <Card className="sticky top-8">
              <CardHeader>
                <CardTitle>Fare Estimate</CardTitle>
                <CardDescription>Estimated cost for your trip</CardDescription>
              </CardHeader>
              <CardContent>
                {estimating ? (
                  <div className="text-center py-8">
                    <div className="w-8 h-8 border-4 border-black border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
                    <p className="text-gray-600">Calculating fare...</p>
                  </div>
                ) : fareEstimate ? (
                  <div className="space-y-4">
                    <div className="flex justify-between">
                      <span className="text-gray-600">Distance:</span>
                      <span className="font-medium">{fareEstimate.distance}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">Duration:</span>
                      <span className="font-medium">{fareEstimate.duration}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">Base Fare:</span>
                      <span className="font-medium">${fareEstimate.baseFare.toFixed(2)}</span>
                    </div>
                    {formData.vehicleType && vehicleTypes.find(v => v.id === formData.vehicleType)?.multiplier !== 1.0 && (
                      <div className="flex justify-between">
                        <span className="text-gray-600">Vehicle Premium:</span>
                        <span className="font-medium">
                          {((vehicleTypes.find(v => v.id === formData.vehicleType)?.multiplier || 1) - 1) * 100}%
                        </span>
                      </div>
                    )}
                    <div className="border-t pt-4">
                      <div className="flex justify-between items-center">
                        <span className="text-lg font-semibold">Total:</span>
                        <span className="text-2xl font-bold">${fareEstimate.totalFare.toFixed(2)}</span>
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="text-center py-8">
                    <div className="w-16 h-16 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                      <span className="text-gray-400 text-2xl">$</span>
                    </div>
                    <p className="text-gray-600">Enter pickup and destination to see fare estimate</p>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}
